import { AUTH_START, AUTH_SUCCESS, AUTH_FAIL ,LOG_OUT} from "./actionTypes";
import axios from "axios";
function authStart(){
return {
    type:AUTH_START,
    paylaod:{isLoading:true,}
    
}
}
function authSuccess(data){
return {
   type:AUTH_SUCCESS,
   payload:{isLoading:false,data}
}
}
function authFail(errorMessage){
    
return {
    type:AUTH_FAIL,
    payload:{
        isLoading:false,
        error:errorMessage
    }
}
}
export const logout=(endpoint)=>{
return function (dispatch)
{
    axios.get(`/api/${endpoint}`).then((response)=>{
        dispatch({type:LOG_OUT});
    })
}
}
export const checkLogin=(endpoint)=>{
return function(dispatch)
{axios.get(`/api/${endpoint}`).then((response)=>{
     let {data}=response;
     console.log(data);
     if(data.code===200 && data.success)
     {
         dispatch(authSuccess(data));
     }else{
         dispatch(authFail(data.message));
     }
}).catch((err)=>{
    let error=new Error("internal error")
    dispatch(authFail(error.message));
})
}
}
export const auth=function(payload,endpoint,setSubmitting){
    return function(dispatch){
        dispatch(authStart());
     axios.post(`/api/${endpoint}`,{...payload}).then((response)=>{
        let {data}=response; 
        console.log(data);
        if(data.code===200 && data.success===true)
         {
           dispatch(authSuccess(data));
         }else{
             dispatch(authFail(data.message))
         }
         setSubmitting(false);
     }).catch(()=>{
         let error=new Error("Some internal Error Occured");
         setSubmitting(false);
         dispatch(authFail(error.message));
     })
    }
}