import React from "react";
import {Formik} from "formik";
import axios from "../../utils/axios";
import Input from "../UI/Input/Input";
class Contact extends React.Component{

 state={
     isLoading:false,
     error:null,
     data:null,
     showAlert:false
 } 
 handleAlert=()=>{
     this.setState({showAlert:false})
 }  
render(){    
return (
    <Formik
    initialValues={{ name: '', email: '' ,message:''}}
    validate={values => {
      let errors = {};
      if (!values.email) {
        errors.email = 'Required';
      } else if (
        !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
      ) {
        errors.email = 'Invalid email address';
      }else if(!values.name){
          errors.name="Required"
      }
      return errors;
    }}
    onSubmit={(values, { setSubmitting }) => {
      this.setState({isLoading:true});
     axios.post("/api/contact",values).then((response)=>{
         let {data}=response;
        if(data.code!==200 && data.success===false)
        {
         this.setState({error:data.message,isLoading:false,showAlert:true})
        } else
        {
        this.setState({data,isLoading:false});
         setSubmitting(false);
        }}).catch((err)=>{
            this.setState({isLoading:false,error:"Server Error"})
        })        
      
    }}
  >
    {({
      values,
      errors,
      touched,
      handleChange,
      handleBlur,
      handleSubmit,
      isSubmitting,
      /* and other goodies */
    }) => (
   this.state.data && this.state.data.success?<h1>Thankyou For Contacting us we will reach to you shortly</h1>:<React.Fragment><h1 style={{textAlign:"center"}}>Contact Us</h1>
   <form className="container" onSubmit={handleSubmit} >
   <div className="row">
   {
       this.state.error?<div className="col-md-6 offset-md-3 alert alert-warning alert-dismissible fade show" style={this.state.showAlert?{display:"block"}:{display:"none"}}>
       {this.state.error}<button type="button" onClick={this.handleAlert} className="close" aria-label="Close">
         <span aria-hidden="true">&times;</span>
       </button>
     </div>:null
   }
   <Input inputName="name" errors={errors} touched={touched} elementType="input" label="Name" elementConfig={{value:values.name,onChange:handleChange,onBlur:handleBlur,type:"text",placeholder:"Enter Name Here"
}}/> 
   <Input inputName="email" errors={errors} touched={touched} elementType="input" label="Email" elementConfig={{value:values.email,onChange:handleChange,onBlur:handleBlur,type:"email",placeholder:"Enter Email Here"
   }}/>
  <Input inputName="message" errors={errors} touched={touched} elementType="textarea" label="Message" elementConfig={{value:values.message,onChange:handleChange,onBlur:handleBlur,rows:"3"}} />
  <div className="col-md-6 offset-3">
    <button type="submit" className="btn btn-info">{`submit${this.state.isLoading?"ting...":""}`}</button>
    </div>
    </div>  
    </form>
    </React.Fragment>
   
     
     )}

    </Formik>
    
)}
}
export default Contact;
