import { SUCCESS ,FAIL } from "../actions/actionTypes";

export default function(state={data:null,error:null},action)
{
    switch(action.type)
    {
        case SUCCESS:
        return {...state,error:null,...action.payload}
        case FAIL:
        return {...state,data:null,...action.payload}
        default:
        return {...state}
    }
} 