import React from 'react'
import io from "socket.io-client";
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {fetchTask} from "../../store/actions/taskActionGenerators";
class Task extends React.Component {
    constructor(props)
    {
        super(props);
        console.log(this.props);
        this.username=(this.props.auth.data && this.props.auth.data.data.name) || Math.floor(Math.random()*99999).toString()
        this.socket = io("https://lit-depths-22566.herokuapp.com/");
       this.socket.emit("joinRoom",{room:this.props.match.params.id,username:this.username})
    
    }
    state = {
        username: '',
        message: '',
        messages: []
    };
    componentDidMount()
    {this.socket.on('receiveMessage', function(data){
        addMessage(data);
    });
    
    const addMessage = data => {
        console.log(data);
        this.setState({messages: [...this.state.messages, data]});
        console.log(this.state.messages);
    };
        this.props.dispatch(fetchTask(this.props.match.params.id));
          let editor=window.CodeMirror.fromTextArea(document.getElementById("code-screen"),{
            lineNumbers:true,
            theme:"monokai"
          });
          function init(str,revision,clients,serverAdapter){
              editor.setValue(str);
              window.cmClient=new window.EditorClient(
         revision,clients,serverAdapter,new window.CodeMirrorAdapter(editor)         
              )
          }
          this.socket.on("doc",(obj)=>{
              init(obj.str,obj.revision,obj.clients,new window.SocketIOAdapter(this.socket))
          })
        
    }
    send = (e) => {
        e.preventDefault();
        this.socket.emit('sendMessage', {
            username: this.username,
            message: this.state.message
        });
        this.setState({message: ''});
    }
    
    render()
  {this.username=(this.props.auth.data && this.props.auth.data.data.name) || Math.floor(Math.random()*99999).toString()
        
      return (
    <div className="row">
      <div className="col-md-8">
      <textarea id="code-screen"></textarea>
      </div>
      <div className="col-md-4" id="chat-box">
      <div className="card">
      <div className="card-header">
      CHAT ROOM
      <span className="float-right">
      {this.username}
      </span>
      </div>
      <div className="card-body">
      <ul className="list-unstyled" style={{height:"300px",overflowY:"scroll"}} >
     <li className="media">
     <div className="media-body" id="chatbox-listMessages">
     {this.state.messages.map((message,index) => {
        return (
            <div key={index}>{`User-${message.username}`}: {message.message}</div>
        )
    })}
     </div>
     </li>
      </ul>
      </div>
      <div className="card-footer">
      <div className="input-group">
      <input type="text" placeholder="Message" className="form-control" value={this.state.message} onChange={ev => this.setState({message: ev.target.value})}/>
      <span className="input-group-btn">
      <button className="btn btn-primary" onClick={this.send}>SEND</button>
      </span>
      </div>
      </div>
      <div></div>
      </div>
      </div>
    </div>
  )
    }
}
export default withRouter(connect()(Task));