import React from "react";
export default function(props){
let inputElement,{elementType,elementConfig,label,inputName,errors,touched}=props;
switch(elementType)
{
case "input":
inputElement=<input name={inputName} style={errors[inputName] && touched[inputName] && {border:"2px solid red"}}
 className="form-control" id={inputName} aria-describedby="emailHelp" {...elementConfig}/>
break;
case "textarea":
inputElement=  <textarea className="form-control" id={inputName} {...elementConfig}></textarea>
break;   
default:
inputElement=<input/>
}
return (
    <div className="form-group col-md-6 offset-md-3">
    <label className="text-monospace" htmlFor={inputName}>{label}</label>
    {inputElement}
    {props.children}
    {errors[inputName] && touched[inputName] && 
        <span className="text-danger">
        {errors[inputName]}
    </span>}
    </div>
)
}