import { AUTH_START, AUTH_SUCCESS , AUTH_FAIL, LOG_OUT} from "../actions/actionTypes";

export const authReducer=(state={isLoading:false,data:null,error:null},action)=>{
switch(action.type)
{
    case AUTH_START:
    return {...state,...action.payload};
    case AUTH_SUCCESS:
    return {...state,error:null,...action.payload};
    case AUTH_FAIL:
    return {...state,...action.payload};
    case LOG_OUT:
    return {...state,isLoading:false,data:null,error:null}
    default:
    return {...state};
}
}