import React from "react";
import {Route,Switch,Redirect,withRouter} from "react-router-dom";
import {connect} from "react-redux";
import Home from "../views/Home";
import About from "../views/About";
import Contact from "../views/Contact";
import Login from "../views/Login";
import Register from "../views/Register";
import Logout from "../views/Logout";
import CreateTask from "../views/CreateTask";
import Task from "../views/Task";
import AdjacentWrapper from "../../Helpers/AdjacentWrapper";
class AppRoutes extends React.Component{
    render()
    {return (
        <Switch>
        {this.props.auth.data?<AdjacentWrapper>
            <Switch>
            <Route path="/" exact render={()=><Home auth={this.props.auth}/>}/>
            <Route path="/about" component={About}/>
            <Route path="/contact" component={Contact}/>
            <Route path="/logout" render={()=><Logout {...this.props}/>}/>
            <Route path="/createTask" render={()=><CreateTask {...this.props}/>}/>
            <Route path="/task/:id" render={()=><Task {...this.props}/>}/>
            
            <Redirect to="/"/>
            </Switch>
            </AdjacentWrapper>:<AdjacentWrapper>
            <Switch>
            <Route path="/" exact render={()=><Home auth={this.props.auth}/>}/>
            <Route path="/about" component={About}/>
            <Route path="/contact" component={Contact}/>
            <Route path="/login" render={()=><Login {...this.props}/>}/>
            <Route path="/register" render={()=><Register {...this.props}/>}/>
            <Route path="/task/:id" render={()=><Task {...this.props}/>}/>
            
            <Redirect to="/"/>
            </Switch>
   </AdjacentWrapper>
        }
        
        
    </Switch>
        )
}
}
const mapStateToProps=(state)=>{
return {"auth":state.auth,"task":state.task}
}
export default withRouter(connect(mapStateToProps)(AppRoutes));