import React from "react";
import {NavLink} from "react-router-dom";
import {connect} from "react-redux";
import NavItem from "./NavItem/NavItem";
const NavItems=(props)=>(
    <nav className="navbar navbar-expand-lg navbar-expand-xl navbar-dark bg-dark">
    <NavLink exact className="navbar-brand" to="/">Code4Share</NavLink>
    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav">
      {props.items.map((item)=>{
          return <NavItem item={item} key={item.linkName}/>
      })}

      </ul>

      {props.auth.data?<div className="ml-auto btn-group">
          <button type="button" className="btn btn-success dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            {props.auth.data.data.name}
          </button>
          <div className="dropdown-menu">
            <NavLink exact className="dropdown-item" to="/logout">Logout</NavLink>
          </div>
        </div>:<ul className="nav navbar-nav ml-auto">
      <li className="nav-item"><NavLink exact className="nav-link" to="/login">Login</NavLink></li>
      <li className="nav-item"><NavLink exact className="nav-link" to="/register">Register</NavLink></li>
      </ul>}
    </div>
  </nav>
)
const mapStateToProps=(state)=>{
  return {"auth":state.auth}
}
export default connect(mapStateToProps)(NavItems);