import React from "react";
import {Formik} from "formik";
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";
import Input from "../UI/Input/Input";
import { auth } from "../../store/actions/authActionGenerators";
class Login extends React.Component{
render(){
    return (
    
    <Formik
      initialValues={{ email: '', password: '' }}
      validate={values => {
        let errors = {};
        if (!values.email) {
          errors.email = 'Required';
        } else if (
          !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
        ) {
          errors.email = 'Invalid email address';
        }if(!values.password)
        {
            errors.password="Required"
        }
        return errors;
      }}
      onSubmit={(values, { setSubmitting }) => {
        this.props.authenticate(values,"login",setSubmitting);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
        /* and other goodies */
      }) => (
        <form className="container" onSubmit={handleSubmit}>
        {this.props.auth.error && <div className="alert alert-danger">{this.props.auth.error}</div>}
        <h1 className="text-center">Please Sign In</h1>
        <div className="row">
      
    <Input inputName="email" errors={errors} touched={touched} elementType="input" label="Email" elementConfig={{value:values.email,onChange:handleChange,onBlur:handleBlur,type:"email",placeholder:"Enter Email Here"
}}/> 
<Input inputName="password" errors={errors} touched={touched} elementType="input" label="password" elementConfig={{value:values.password,onChange:handleChange,onBlur:handleBlur,type:"password",placeholder:"Enter password Here"
}}/> 

<div className="col-md-6 col-sm-6 col-6 offset-3">
<button type="submit" className="btn btn-info btn-block">Login</button>
</div>
<h4 className="col-md-6 col-sm-6 col-6 offset-3 text-center mt-3">Or</h4>
<div className="col-md-6 col-sm-6 col-6 offset-3 mt-3">
<a href="/auth/facebook" className="btn bg-success btn-info btn-block">Login with Facebook</a></div>
          </div>
        </form>
      )}
    </Formik>
  
    )
}
}

const mapDispatchToProps=(dispatch)=>{
  return {"authenticate":(data,endpoint,fn)=>dispatch(auth(data,endpoint,fn))}
}
export default withRouter(connect(null,mapDispatchToProps)(Login));