import axios from "axios";
import { SUCCESS, FAIL } from "./actionTypes";
function success(data)
{
    return {type:SUCCESS,payload:{data}}
}
function fail(error)
{
    return {type:FAIL,payload:{error}}
}
export function createTask(){
return function(dispatch)
{console.log("calling create Task------");
    axios.get("/api/createTask").then((response)=>{
    let {data}=response;
    if(data.code===200 && data.success)
    {
        dispatch(success(data));
    }else{
     dispatch(fail(data.message));
    }
}).catch(()=>{
    dispatch(fail("some internal error occured"));
})
}}
export function fetchTask(taskId){
    return function(dispatch)
    {
        axios.get(`/api/task/${taskId}`).then((response)=>{
            let {data}=response;
            if(data.code===200 && data.success)
    {
        dispatch(success(data));
    }else{
        dispatch(fail(data.message));
    }
        }).catch(()=>{
         dispatch(fail("some internal error occured"));
       })

    }
}