import React from "react";
import {Formik} from "formik";
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";
import Input from "../UI/Input/Input";
import {auth} from "../../store/actions/authActionGenerators";
class Register extends React.Component{
render(){
    return (
    
    <Formik
      initialValues={{ name: '', email: '', password: '' ,confirmPassword: ''}}
      validate={values => {
        let errors = {};
        if (!values.email) {
          errors.email = 'Required';
        } else if (
          !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
        ) {
          errors.email = 'Invalid email address';
        }if(!values.name){
            errors.name="Required"
        }if(!values.password)
        {
            errors.password="Required"
        }if(values.password!==values.confirmPassword){
         errors.confirmPassword="password didn't match"
        }
        return errors;
      }}
      onSubmit={(values, { setSubmitting }) => {
        this.props.register(values,"register",setSubmitting);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
        /* and other goodies */
      }) => (
        <form className="container" onSubmit={handleSubmit}>
        <h1 className="text-center">New Account</h1>
        <div className="row">
        <Input inputName="name" errors={errors} touched={touched} elementType="input" label="Name" elementConfig={{value:values.name,onChange:handleChange,onBlur:handleBlur,type:"text",placeholder:"Enter Name Here"
    }}/>   
    <Input inputName="email" errors={errors} touched={touched} elementType="input" label="Email" elementConfig={{value:values.email,onChange:handleChange,onBlur:handleBlur,type:"email",placeholder:"Enter Email Here"
}}/> 
<Input inputName="password" errors={errors} touched={touched} elementType="input" label="password" elementConfig={{value:values.password,onChange:handleChange,onBlur:handleBlur,type:"password",placeholder:"Enter password Here"
}}/> 
<Input inputName="confirmPassword" errors={errors} touched={touched} elementType="input" label="confirmPassword" elementConfig={{value:values.confirmPassword,onChange:handleChange,onBlur:handleBlur,type:"password",placeholder:"Re-enter password"
}}/> 
<div className="col-sm-6 col-6 col-md-6 offset-3">
<button type="submit" className="btn btn-info btn-block">SignUp</button>
</div>
          </div>
        </form>
      )}
    </Formik>
  
    )
}
}

const mapDispatchToProps=(dispatch)=>{
  return {"register":(data,endpoint,setSubmitting)=>dispatch(auth(data,endpoint,setSubmitting))}
}
export default withRouter(connect(null,mapDispatchToProps)(Register));