import React from 'react';
import {Redirect,withRouter} from "react-router-dom";
import {connect} from "react-redux";
import { createTask } from '../../store/actions/taskActionGenerators';

const mapDispatchToProps=(dispatch)=>
{
    return {
     "createTask":()=>dispatch(createTask())
    }
}
export default withRouter(connect(null,mapDispatchToProps)((props) => {
    props.createTask();
    console.log(props);
  let itemToRender=<h1>Creating Task......</h1>
    if(props.task.data)
  {console.log("task------",props.task);
    itemToRender=<Redirect to={`/task/${props.task.data.data._id}`}/>
}return (
    <div>
   {itemToRender}   
    </div>
  )
}))


